






import { Component, Vue } from 'vue-property-decorator'
import EventService from '@/services/EventService'
import { IEventDto, IRegisteredUser, IRegisterUser } from '@/types/Admin/Event'
import { IHeader } from '@/types/UI/UIStoreTypes'

@Component({
  name: 'SandbaggerEvent',
  components: {
    Loading: (): Promise<object> => import('@/components/ui/Loading.vue'),
  },
})
export default class SandbaggerEvent extends Vue {
  loading = false
  Event = {} as IEventDto
  currentUser = {} as IRegisterUser
  async mounted(): Promise<void> {
    await this.getEventById()
  }

  get isCurrentUserAlreadyRegistered(): boolean {
    if (this.Event && this.Event.registeredUsers) {
      const alreadyRegistered = this.Event.registeredUsers.find((u) => u.id === this.currentUser.id)
      if (alreadyRegistered) {
        return true
      } else {
        return false
      }
    } else {
      return false
    }
  }
  async registerForEvent(): Promise<void> {
    this.loading = true
    try {
      const res = await EventService.RegisterUserForEvent(this.currentUser)
      if (res.status === 200) {
        const registeredUser: IRegisteredUser = {
          fullName: this.currentUser.fullName,
          id: this.currentUser.id,
          username: this.currentUser.username,
          image: this.$store.state.authStore.currentUser.profile.image,
        }
        this.Event.registeredUsers.push(registeredUser)
      }
      this.loading = false
    } catch (e) {
      this.loading = false
      console.log(e)
    }
  }

  async getEventById(): Promise<void> {
    this.loading = true
    try {
      const res = await EventService.getEventById(this.$route.params.eventId)
      this.Event = res.data
      this.currentUser = {
        eventId: this.Event.eventId.toString(),
        id: this.$store.state.authStore.currentUser.id,
        username: this.$store.state.authStore.currentUser.username,
        fullName: this.$store.state.authStore.currentUser.fullName,
      }

      await this.$store.dispatch('uiStore/_setHeader', { current: 'main', isShowing: true, title: res.data.name } as IHeader)

      this.loading = false
    } catch (e) {
      this.loading = false
      console.log(e)
    }
  }
}
